:root {
  --bulma-info-h: 197;
  --bulma-link-h: 257;
}

#app .navitem-right {
  margin-inline: 0;
  padding-left: 0;
  padding-right: 0;
}

#app .navitem-right input, #app .navitem-right .button {
  border-left-width: 0;
  border-radius: 0;
  max-width: min(90vw - 17rem, 200px);
}

#app .navitem-right:first-of-type {
  margin-inline-start: auto;
}

#app .navitem-right:first-of-type input, #app .navitem-right:first-of-type .button {
  border-top-left-radius: var(--bulma-radius);
  border-bottom-left-radius: var(--bulma-radius);
  border-left-width: 1px;
}

#app .navitem-right:last-of-type {
  margin-inline-end: .375rem;
}

#app .navitem-right:last-of-type input, #app .navitem-right:last-of-type .button {
  border-top-right-radius: var(--bulma-radius);
  border-bottom-right-radius: var(--bulma-radius);
}

html #app nav.navbar {
  border-bottom: 2px solid var(--bulma-border);
  padding-left: 0;
  padding-right: 0;
}

html #app {
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

html.has-navbar-fixed-top #app {
  min-height: 100vh;
}

html #app {
  background-color: hsl(var(--bulma-info-h), var(--bulma-info-s), var(--bulma-background-l));
}

html #app code, html #app pre, html #app tt, html #app kbd, html #app samp, html #app .mono {
  font-family: FiraCode Nerd Font;
}

html #app .container {
  background-color: var(--bulma-scheme-main);
  border-radius: 0;
  max-width: min(90vw, 1152px);
}

@media screen and (width >= 1024px) {
  html #app nav.navbar {
    padding-left: .75rem;
    padding-right: .75rem;
  }

  html #app table.table {
    max-width: 100%;
  }
}

.has-background-danger-op-1 {
  background-color: hsl(var(--bulma-danger-h), var(--bulma-danger-s), var(--bulma-danger-on-scheme-l), .05) !important;
}

.has-background-link-op-1 {
  background-color: hsl(var(--bulma-link-h), var(--bulma-link-s), var(--bulma-link-on-scheme-l), .05) !important;
}

.has-background-warning-op-1 {
  background-color: hsl(var(--bulma-warning-h), var(--bulma-warning-s), var(--bulma-warning-on-scheme-l), .05) !important;
}

html #app .wmax, html #app .hmax {
  width: 100% !important;
}

html #app .pe-none {
  pointer-events: none !important;
}

html #app .rel {
  position: relative !important;
}

html #app .ultra-faint {
  opacity: .90625 !important;
}

html #app .navbar-item.is-selected, html #app .navbar-link.is-selected {
  color: hsl(var(--bulma-link-h), var(--bulma-link-s), var(--bulma-link-invert-l));
}

html #app > .container {
  padding-bottom: 0 !important;
  padding-top: calc(var(--bulma-navbar-height) + 2px) !important;
}

html #app .has-text-weak {
  color: var(--bulma-text-weak) !important;
}

html #app .has-text-strong {
  color: var(--bulma-text-strong) !important;
}

html #app tr td {
  vertical-align: middle;
}

html #app > div {
  padding-top: 2rem;
}

html #app .player-info > :first-child {
  width: 50px;
  min-width: 50px;
  max-width: 50px;
  height: 50px;
  min-height: 50px;
  max-height: 50px;
  filter: drop-shadow(0 0 .125rem var(--bulma-border));
  border-radius: 50px;
  position: relative;
  overflow: hidden;
}

html #app .player-info > :first-child img {
  object-fit: cover;
  background-color: var(--bulma-border);
  color: #0000;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

html #app .player-info > :last-child {
  margin-left: 1rem;
  overflow: hidden;
}

html #app .player-info > :last-child > div {
  color: var(--bulma-table-color);
}

html #app .player-info > :last-child > div:first-child {
  color: var(--bulma-link);
  margin-bottom: -.25rem;
}

html #app .player-info:hover > :first-child {
  filter: drop-shadow(0 0 .125rem var(--bulma-link-dark-invert));
}

html #app .player-info:hover > :last-child > div:first-child {
  text-decoration: underline;
}

.ws-nw {
  white-space: nowrap !important;
}

html #app .nowrap {
  white-space: nowrap !important;
  text-overflow: ellipsis !important;
  overflow: hidden !important;
}

html #app table.table {
  table-layout: fixed;
  border-collapse: separate;
  border-spacing: 0;
  background-color: var(--bulma-scheme-main-bis);
  min-width: 100%;
  position: relative;
}

html #app table.table a:hover {
  text-decoration: underline;
}

html #app table.table th {
  z-index: 1;
  background-color: var(--bulma-scheme-main-ter);
  border-bottom: 2px solid var(--bulma-border);
  vertical-align: middle;
  position: sticky;
  top: 0;
}

html #app table.table th .curr-sort {
  opacity: 1;
  transition: opacity .5s ease-in-out;
}

html #app table.table th .next-sort {
  opacity: 0;
  transition: opacity .5s ease-in-out;
}

html #app table.table th a:hover {
  text-decoration: underline;
  color: var(--bulma-link) !important;
}

html #app table.table th a:hover .curr-sort {
  opacity: 0;
  color: var(--bulma-text) !important;
}

html #app table.table th a:hover .curr-sort .mono {
  color: var(--bulma-text) !important;
}

html #app table.table th a:hover .isActive > .curr-sort {
  opacity: .25;
}

html #app table.table th a:hover .next-sort {
  opacity: 1;
}

html #app table.table td, html #app table.table th {
  white-space: nowrap;
  width: 1%;
}

html #app table.table td:nth-child(2), html #app table.table th:nth-child(2) {
  min-width: 20rem;
  width: auto !important;
}

html #app table.table td:first-child {
  position: relative;
}

html #app table.table td:first-child > div {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

html #app table.table td:last-child a:hover {
  text-decoration: underline;
}

html #app table.table th:last-child {
  position: sticky;
}

@media (prefers-color-scheme: dark) {
  img.defaultProfile {
    filter: brightness(55%) contrast(1000%);
  }
}
